<template>
  <div id="app" >
    <router-view> </router-view>    
    
  </div>
</template>

<script>
export default {
  name: 'app',
  components: {},
  data () {
    return {

    }
  },
  methods: {
    init () {

    }
  },
  mounted () {
    this.init()
  },
  watch: {
    $route (to, from) {

    }
  }
}
</script>

<style lang="less">
  *{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin:0px;
    padding:0px;
  }
  .el-dialog{
    border-radius: 5px;
  }
  .bounce-enter-active {
    animation: bounce-in .5s;
    -webkit-animation:bounce-in .5s;
  }

  .bounce-leave-active {
    animation: bounce-out .2s;
    -webkit-animation: bounce-out .2s;
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.05);
    }
    100% {
      transform: scale(1);
    }
  }

  @keyframes bounce-out {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(0.95);
    }
    100% {
      transform: scale(0);
    }
  }
  .el-button--info,.el-button--primary {
    color: #fff;
    background-color: #0F78EE;
    border-color: #0F78EE;
  }
</style>



